// services/company.service.ts
import api from "./api";
import { BranchFormData } from "@/components/Company/BranchFormModal";
import {  CategoryFormData } from "@/components/Company/CategoryFormModal";
import {  CategoryTypeFormDataType } from "@/components/Company/CategoryTypeFormModal";
import {  StockFormData } from "@/components/Inventory/StockFormModal";

import {  StockTransferFormData } from "@/components/Inventory/StockTransferkFormModal";
import {  StockConvertUnitFormData } from "@/components/Inventory/StockConvertFormModal";

export const ProductService = {

     getAllStocks() {
    return api.get("/Product/stock-list");
  },

       getproduct() {
    return api.get("/Product/products");
  },


      // CREATE BRANCH (JSON)
  CreateStockAsync(data: StockFormData) {
    return api.post("/Product/create-ReceiveStockAsync", data);
  },



        // CREATE BRANCH (JSON)
  CreateStockTransfrerAsyn(data: StockTransferFormData) {
    return api.post("/Product/create-StockTransfer", data);
  },



          // CREATE BRANCH (JSON)
  CreateStockConvertAsyn(data: StockConvertUnitFormData) {
    return api.post("/Product/create-convertStock", data);
  },

};
