"use client";

import React, { useEffect, useRef, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import StockFormModal, { StockFormData } from "./StockFormModal";
import ConfirmDeleteModal from "../Company/ConfirmDeleteModal";
import { CompanyService } from "@/services/company.service";
import { ProductService } from "@/services/product.service";

import toast from "react-hot-toast";

/* =========================
   Types
========================= */
interface StockDto {
  id: string;
  productId: string;
  productName: string;
  branchName: string;
  quantity: string;
  isLowStock: boolean;
  createdAt?: string;
  createdBy?: string;
  unit?: string;
}

/* =========================
   Action Menu
========================= */
function ActionMenu({
  openUp,
  onEdit,
  onDelete,
}: {
  openUp?: boolean;
  onEdit: () => void;
  onDelete: () => void;
}) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    function handleClickOutside(e: MouseEvent) {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        setOpen(false);
      }
    }
    if (open) document.addEventListener("mousedown", handleClickOutside);
    return () =>
      document.removeEventListener("mousedown", handleClickOutside);
  }, [open]);

  return (
    <div ref={ref} className="relative">
      <button
        onClick={() => setOpen((p) => !p)}
        className="rounded-full p-2 hover:bg-gray-100 dark:hover:bg-gray-700"
      >
        ‚ãÆ
      </button>

      {open && (
        <div
          className={`absolute right-0 z-50 w-36 rounded-lg border bg-white shadow-lg
          dark:border-gray-700 dark:bg-gray-900
          ${openUp ? "bottom-full mb-2" : "top-full mt-2"}`}
        >
          <button
            onClick={() => {
              onEdit();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm hover:bg-gray-50 dark:hover:bg-gray-800"
          >
            ‚úèÔ∏è Edit
          </button>

          <button
            onClick={() => {
              onDelete();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20"
          >
            üóë Delete
          </button>
        </div>
      )}
    </div>
  );
}

/* =========================
   Stock Table
========================= */
export default function StockTable() {
  const [stocks, setStocks] = useState<StockDto[]>([]);
  const [search, setSearch] = useState("");

  const [openModal, setOpenModal] = useState(false);
  const [mode, setMode] = useState<"add" | "edit">("add");
  const [selectedStock, setSelectedStock] = useState<StockDto | null>(null);

  const [openDelete, setOpenDelete] = useState(false);
  const [deleting, setDeleting] = useState(false);
  const [loading, setLoading] = useState(false);

  /* =========================
     LOAD STOCKS
  ========================= */
  async function loadStocks() {
    setLoading(true);
    try {
      const res = await ProductService.getAllStocks();
      setStocks(res.data.data);
    } catch (err: any) {
      toast.error("Failed to load stocks");
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    loadStocks();
  }, []);

  /* =========================
     ADD / UPDATE
  ========================= */
  async function handleSubmit(data: StockFormData) {
    try {
      const payload = {
        productId: data.productId,
        branchId: data.branchId,
        quantity: data.quantity,
      };

      if (mode === "add") {
        await ProductService.CreateStockAsync(payload);
        toast.success("Stock added successfully");
      } else {
        if (!selectedStock?.id) return;
        //await CompanyService.updateStock(selectedStock.id, payload);
        toast.success("Stock updated successfully");
      }

      setOpenModal(false);
      loadStocks();
    } catch {
      toast.error("Operation failed");
    }
  }

  /* =========================
     DELETE
  ========================= */
  async function confirmDelete() {
    if (!selectedStock) return;

    try {
      setDeleting(true);
      //await CompanyService.deleteStock(selectedStock.id);
      toast.success("Stock deleted successfully");
      setOpenDelete(false);
      loadStocks();
    } catch {
      toast.error("Failed to delete stock");
    } finally {
      setDeleting(false);
    }
  }

  /* =========================
     FILTER
  ========================= */
  const filteredStocks = stocks.filter(
    (s) =>
      s.productName.toLowerCase().includes(search.toLowerCase()) ||
      s.branchName.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="w-full rounded-xl bg-white px-6 pb-6 dark:bg-gray-900">
      <h2 className="mb-4 text-xl font-semibold text-gray-800 dark:text-white">
        Stock
      </h2>

      {/* Top Bar */}
      <div className="mb-4 flex items-center justify-between">
        <Input
          placeholder="Search stock..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-64"
        />
        <Button
          size="sm"
          onClick={() => {
            setMode("add");
            setSelectedStock(null);
            setOpenModal(true);
          }}
        >
          + Increase Stock
        </Button>
      </div>

      {/* Table */}
      <div className="overflow-x-auto">
        <table className="w-full border-collapse">
          <thead className="bg-gray-50 dark:bg-gray-800">
            <tr className="text-left text-sm">
              <th className="px-3 py-3">Product</th>
              <th className="px-3">Branch</th>
              <th className="px-3">Quantity</th>
              <th className="px-3">Status</th>
              <th className="px-3">Unit</th>
              <th className="px-3 text-right">Action</th>
            </tr>
          </thead>

          <tbody>
            {loading ? (
              <tr>
                <td colSpan={6} className="py-6 text-center">
                  Loading stock...
                </td>
              </tr>
            ) : (
              filteredStocks.map((s, i) => (
                <tr
                  key={s.productId}
                  className="border-t hover:bg-gray-50 dark:hover:bg-gray-800"
                >
                  <td className="x-3 text-sm text-gray-600 dark:text-gray-400">{s.productName}</td>
                  <td className="x-3 text-sm text-gray-600 dark:text-gray-400">{s.branchName}</td>
                  <td className="x-3 text-sm text-gray-600 dark:text-gray-400">{s.quantity}</td>
                  <td className="x-3 text-sm text-gray-600 dark:text-gray-400">
                    {s.isLowStock ? (
                      <span className="text-red-600 text-sm">Low</span>
                    ) : (
                      <span className="text-green-600 text-sm">Normal</span>
                    )}
                  </td>

              <td className="x-3 text-sm text-gray-600 dark:text-gray-400">{s.unit}</td>
                  <td className="px-3 text-right">
                    <ActionMenu
                      openUp={i === filteredStocks.length - 1}
                      onEdit={() => {
                        setMode("edit");
                        setSelectedStock(s);
                        setOpenModal(true);
                      }}
                      onDelete={() => {
                        setSelectedStock(s);
                        setOpenDelete(true);
                      }}
                    />
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {/* MODAL */}
      <StockFormModal
        open={openModal}
        mode={mode}
        initialData={
          selectedStock
            ? {
                productId: selectedStock.productId,
                branchId: "",
                quantity: selectedStock.quantity,
              }
            : undefined
        }
        onClose={() => setOpenModal(false)}
        onSubmit={handleSubmit}
      />

      {/* DELETE */}
      <ConfirmDeleteModal
        open={openDelete}
        loading={deleting}
        onClose={() => setOpenDelete(false)}
        onConfirm={confirmDelete}
      />
    </div>
  );
}
