"use client";
import { useEffect, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import Label from "@/components/form/Label";
import { CompanyService } from "@/services/company.service";
import { ProductService } from "@/services/product.service";
import { toast } from "react-hot-toast";

export interface StockFormData {
  productId: string;
  branchId: string;
  quantity: string;
}

interface Branch {
  id: string;
  branchName: string;
}

interface Product {
  id: string;
  name: string;
}

interface Props {
  open: boolean;
  mode: "add" | "edit";
  initialData?: StockFormData;
  onClose: () => void;
  onSubmit: (data: StockFormData) => void;
}

const emptyForm: StockFormData = {
  productId: "",
  branchId: "",
  quantity: "",
};

export default function StockFormModal({
  open,
  mode,
  initialData,
  onClose,
  onSubmit,
}: Props) {
  const [form, setForm] = useState<StockFormData>(emptyForm);
  const [errors, setErrors] =
    useState<Partial<Record<keyof StockFormData, string>>>({});
  const [loading, setLoading] = useState(false);

  const [Branches, setBranches] = useState<Branch[]>([]);
  const [loadingBranches, setLoadingBranches] = useState(false);

  const [Products, setProducts] = useState<Product[]>([]);
  const [loadingProducts, setLoadingProducts] = useState(false);

  useEffect(() => {
    if (!open) return;

    const fetchBranches = async () => {
      try {
        setLoadingBranches(true);
        const res = await CompanyService.getAllBranches();
        setBranches(res.data.data);
      } catch (err) {
        console.error("Failed to load branches", err);
        setBranches([]);
      } finally {
        setLoadingBranches(false);
      }
    };

    fetchBranches();
  }, [open]);

  useEffect(() => {
    if (!open) return;

    const fetchProducts = async () => {
      try {
        setLoadingProducts(true);
        const res = await ProductService.getproduct();
        setProducts(res.data.data);
      } catch (err) {
        console.error("Failed to load products", err);
        setProducts([]);
      } finally {
        setLoadingProducts(false);
      }
    };

    fetchProducts();
  }, [open]);

  useEffect(() => {
    setForm(mode === "edit" && initialData ? initialData : emptyForm);
    setErrors({});
  }, [mode, initialData, open]);

  useEffect(() => {
    const esc = (e: KeyboardEvent) => e.key === "Escape" && onClose();
    document.addEventListener("keydown", esc);
    return () => document.removeEventListener("keydown", esc);
  }, [onClose]);

  const update = <K extends keyof StockFormData>(
    key: K,
    value: StockFormData[K]
  ) => {
    setForm((prev) => ({
      ...prev,
      [key]: value,
    }));

    setErrors((prev) => ({
      ...prev,
      [key]: undefined,
    }));
  };

  const validate = () => {
    const e: typeof errors = {};

    if (!form.branchId.trim()) {
      e.branchId = "Branch is required";
      toast.error(e.branchId);
    }

    if (!form.productId.trim()) {
      e.productId = "Product is required";
      toast.error(e.productId);
    }

    if (!form.quantity.trim()) {
      e.quantity = "Quantity is required";
      toast.error(e.quantity);
    }

    setErrors(e);
    return Object.keys(e).length === 0;
  };

  const submit = async () => {
    if (!validate() || loading) return;
    setLoading(true);
    await onSubmit(form);
    setLoading(false);
  };

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
      <div
        className="w-full max-w-3xl bg-white dark:bg-gray-900 rounded-xl shadow-xl"
        onClick={(e) => e.stopPropagation()}
      >
        {/* Header */}
        <div className="relative p-5 border-b dark:border-gray-800">
          <h3 className="text-lg font-semibold text-center text-gray-900 dark:text-white">
            {mode === "add" ? "Add Branch" : "Edit Branch"}
          </h3>
          <button
            onClick={onClose}
            className="absolute right-5 top-1/2 -translate-y-1/2 text-2xl text-gray-500 hover:text-gray-700"
          >
            √ó
          </button>
        </div>

        {/* Body */}
        <div className="p-6 grid grid-cols-1 md:grid-cols-2 gap-6">
          <Field label="Branch" required error={errors.branchId}>
            <select
              className="w-full rounded-md border px-3 py-2 dark:bg-gray-800"
              value={form.branchId}
              onChange={(e) => update("branchId", e.target.value)}
              disabled={loadingBranches}
            >
              <option value="">
                {loadingBranches ? "Loading branches..." : "Select branch"}
              </option>
              {Branches.map((branch) => (
                <option key={branch.id} value={branch.id}>
                  {branch.branchName}
                </option>
              ))}
            </select>
          </Field>

          <Field label="Product" required error={errors.productId}>
            <select
              className="w-full rounded-md border px-3 py-2 dark:bg-gray-800"
              value={form.productId}
              onChange={(e) => update("productId", e.target.value)}
              disabled={loadingProducts}
            >
              <option value="">
                {loadingProducts ? "Loading products..." : "Select product"}
              </option>
              {Products.map((product) => (
                <option key={product.id} value={product.id}>
                  {product.name}
                </option>
              ))}
            </select>
          </Field>

          <Field label="Quantity" required error={errors.quantity}>
            <Input
              value={form.quantity}
              onChange={(e) => update("quantity", e.target.value)}
            />
          </Field>
        </div>

        {/* Footer */}
        <div className="p-5 border-t dark:border-gray-800 flex justify-end gap-3">
          <Button variant="outline" onClick={onClose}>
            Cancel
          </Button>
          <Button onClick={submit} disabled={loading}>
            {loading ? "Saving..." : mode === "add" ? "Create" : "Update"}
          </Button>
        </div>
      </div>
    </div>
  );
}

/* ---------- Small Helper ---------- */
function Field({
  label,
  children,
  error,
  required,
  full,
}: {
  label: string;
  children: React.ReactNode;
  error?: string;
  required?: boolean;
  full?: boolean;
}) {
  return (
    <div className={full ? "md:col-span-2" : ""}>
      <Label>
        {label} {required && <span className="text-red-500">*</span>}
      </Label>
      {children}
      {error && <p className="text-sm text-red-600 mt-1">{error}</p>}
    </div>
  );
}
